function addHoverBtn() {
    if (userCheck && passCheck && emailCheck) {
        console.log('add')
        document.querySelector('.form_submit').classList.add('form_submit_active')
    }
    else {
        console.log('no add')
        if(document.querySelector('.form_submit').classList.contains('form_submit_active')){
            document.querySelector('.form_submit').classList.remove('form_submit_active')
        }
    }
}
user.oninput = function () {
    username = user.value
    if (user.value) {
        userCheck = true
        addHoverBtn()
    }
    if (!user.value) {
        userCheck = false
        addHoverBtn()
    }
};
pass.oninput = function () {
    password = pass.value
    if (pass.value === 'PEEXI-2025-BCQYZ-RTXGV') {
        passCheck = true
        addHoverBtn()
    }
    if (!pass.value) {
        passCheck = false
        addHoverBtn()
    }
};
email.oninput = function () {
    mail = email.value
    const emailRegex = /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu;

    const isValidEmail = emailRegex.test(email.value);
    if (isValidEmail) {
        emailCheck = true
        addHoverBtn()
    }
    if (!isValidEmail) {
        emailCheck = false
        addHoverBtn()
    }
};


